import React from 'react';
import { Container } from 'react-bootstrap';
import { BookFill,Calendar3 } from 'react-bootstrap-icons';


export default class Rightdown extends React.Component{
    render(){
        const compstyle={
            boxShadow:"3px 3px 3px grey"
          }
            return (
            <>  
              <div className="w3-white w3-margin-bottom text-muted" style={compstyle}>
               <Container>
               <br />
               <h4> <BookFill color="green" size={30} /> Education</h4>
               <br />
               <br />
               <h5>B.Tech, Electronics and Communications Engineering (ECE)</h5>
                   <p className="w3-text-green"><Calendar3 color="green" size={20} /> 2019-23  </p>
                   <br />
                  From Bhagwan Parshuram Institute Of Technology, GGSIPU.
                   <br />
               <br />
                  <h5>Senior Secondary</h5>
                   <p className="w3-text-green"><Calendar3 color="green" size={20} /> 2018 </p>
                   <br />
                   12th from Govt. Sarvodaya Bal Vidyalaya, CBSE.
               <br />
               <br />
               </Container>
              </div>
            </>  
        );
    }
}