import React from 'react';
import { Container  } from 'react-bootstrap';
import { BriefcaseFill } from 'react-bootstrap-icons';

export default class Rightupper extends React.Component{
    render(){
      const compstyle={
        boxShadow:"3px 3px 3px grey"
      }
        return (
        <>  


          <div className="w3-white w3-margin-bottom text-muted" style={compstyle}>
            <Container>
            <br />
           <h4> <BriefcaseFill color="green" size={30} /> Programming experience</h4>
           <br />
           <br />
           <div class="w3-container w3-red w3-cell w3-border-white w3-leftbar w3-bottombar w3-hover-border-green w3-round-xlarge ">
            <p className="mt-2">C Programming Language</p>
           </div>
           <br />
           <br />
           <div class="w3-container w3-red w3-cell w3-border-white w3-leftbar w3-bottombar w3-hover-border-green w3-round-xlarge ">
            <p className="mt-2">JAVA Programming Language</p>
           </div>
           <br />
           <br />
           <div class="w3-container w3-red w3-cell w3-border-white w3-leftbar w3-bottombar w3-hover-border-green w3-round-xlarge ">
            <p className="mt-2">JavaScript, Scripting Language</p>
           </div>
           <br />
           <br />
           <div class="w3-container w3-red w3-cell w3-border-white w3-leftbar w3-bottombar w3-hover-border-green w3-round-xlarge ">
            <p className="mt-2">Python Programming Language</p>
           </div>
           <br />
           <br />
            </Container>
          </div>
        </>  
        );
    }
}