import React from 'react';
import { Container } from 'react-bootstrap';
import { Translate } from 'react-bootstrap-icons';

export default class Rightdown2 extends React.Component{
    render(){
        const compstyle={
            boxShadow:"3px 3px 3px grey"
          }
          const mystyle2={
            width:"70%",
            backgroundColor:"green"
          }
          const mystyle3={
            width:"100%",
            backgroundColor:"green"
          }
        return (
            <>
              <div className="w3-white w3-margin-bottom text-muted" style={compstyle}>
               <Container>
                <br />
                <h4><Translate color="green" size={20} /> Languages</h4>
                <br />
                English
                <br />
                <br />
                <div class="w3-light-grey w3-round-xlarge">
                        <div class="w3-round-xlarge w3-text-white w3-center" style={mystyle2}>70%</div>
                </div>
                <br />
            
                Hindi
                <br />
                <br />
                <div class="w3-light-grey w3-round-xlarge">
                        <div class="w3-round-xlarge w3-text-white w3-center" style={mystyle3}>100%</div>
                </div>
                <br />
                <br />
               </Container>    
              </div>  
            </>
        );
    }
}