import React from 'react'
import './App.css';
import { Container,Row,Col } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css'
import Leftcomp from './mycomponents/Leftcomp';
import Footer from './mycomponents/Footer';
import Rightdown from './mycomponents/Rightdown'
import Rightupper from './mycomponents/Rightupper'
import Rightdown2 from './mycomponents/Rightdown2'

 function App() {
  return (
    <>
    <Container>
  <Row>
    <Col sm={4}><Leftcomp /></Col>  <Col sm={8}>
      <Col>
       <Row sm><Rightdown /></Row>
       <Row sm><Rightupper /></Row>
       <Row sm><Rightdown2 /></Row>
      </Col>
    </Col>
   
  </Row>
</Container>
    <br />
    <br />
    <Footer />
  </>
  );
}

export default App;
